@import url('https://fonts.googleapis.com/css2?family=Anonymous+Pro:ital,wght@0,400;0,700;1,400;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anuphan:wght@100..700&display=swap');

.cardContainer{
    margin-top: 100px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 420px;
    height: 420px;
    border-radius: 30px;
    background-color: rgba(31, 31, 31,66%);
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.6), 0 10px 20px 0 rgba(0, 0, 0, 0.19);
}
.cardContainer:hover{
    box-shadow: 0 4px 20px 0 rgba(20, 255, 255, 255.6), 0 10px 20px 0 rgba(0, 0, 0, 0.19);
    transition: .4s;
}
.cardContainer:not(hover){
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.6), 0 10px 20px 0 rgba(0, 0, 0, 0.19);
    transition: .4s;
}
.cardUp{
    height: 40%;
    width: 100%;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    background-size:cover;
    background-repeat: no-repeat;
    background-position: center;

}
.cardDown{
    height: 60%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 20px;
    padding-left: 20px;
    h3{
        color: white;
        font-family: "Anonymous Pro", monospace;
        font-weight: 400;
        font-style: normal;
        font-size: 16px;
    }
    a{
        margin-top: 12px;
        margin-right: 15px;
        font-family: "Anuphan", sans-serif;
        color: white;
        font-size: 22px;
        letter-spacing: 1px;
    }
}

.cardGotoBtnPlace{
    display: flex;
    justify-content: end;
    height: 100%;
    align-items: end;
}

.cardGotoBtn{
    cursor: pointer;
    width: 20px;
    height: 20px;
    margin-right: 25px;
    margin-bottom: 25px;
    background-size: cover;
    background-image: url("../../../../../public/icon-link.png");
}