@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anuphan:wght@100..700&display=swap');


header{
    display: flex;
    flex-direction: row;
    align-items: center;
    max-height: 80px;
    justify-content:space-between;
    background-color: rgba(40, 40, 40,70%);

    -ms-user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}

/*Text*/
.headertext{
    font-family: "DM Sans", sans-serif;
    font-size: 45px;
    padding-left: 34px;
    color: #14FFEC;
    letter-spacing:4px ;
    cursor: pointer;

    a{
        color: white;
    }
}
.headertext:hover{
    color: white;
    transition: .4s;
    a{
        color: #14FFEC;
        transition: .4s;
    }
}
.headertext:not(hover){
    color: #14FFEC;
    transition: .4s;
    a{
        color: white;
        transition: .4s;
    }
}
/*Img*/
.headerimg{
    width: 110px;
    height:110px;
    margin-left:230px ;
}
/*Navigator*/
nav{
    color: white;
    display: flex;
    flex-direction: row;
    text-align: center;
    gap: 30px;
    align-items: center;
    margin-right: 30px;

    font-family: "Anuphan", sans-serif;
    font-weight:100;
    font-size: 16px;
    letter-spacing: 2px;
}