@import url('https://fonts.googleapis.com/css2?family=Anonymous+Pro:ital,wght@0,400;0,700;1,400;1,700&display=swap');

.maincardcontainer{
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    height: 710px;
}

.maincard{
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.6), 0 10px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    flex-direction: row;
    border-radius: 30px;
    height: 100%;
    width: 1500px;
    background-color: rgba(31, 31, 31,66%);
    background-image: url("https://static.wikia.nocookie.net/28b5bc91-53e3-444e-b06d-e99af657821c/scale-to-width/755");
    background-size:cover;
    background-repeat: no-repeat;
    background-position: center;
}

.maincardleft{
    display: flex;
    flex-direction: column;
    justify-content: end;
    border-top-left-radius:30px;
    border-bottom-left-radius:30px;
    width: 50%;
    padding-bottom: 60px;
    padding-left: 40px;
    background-color: rgba(31, 31, 31,66%);

    font-family: "Anonymous Pro", monospace;
    font-weight: 400;
    font-style: normal;
}

.maincardleft a{
    color:#14FFEC;
    letter-spacing: 1px;
    font-size: 42px;
}

.maincardright{
    border-top-right-radius:30px;
    border-bottom-right-radius:30px;
    width: 50%;

    display: flex;
}

.maincardimg{
    background-size: cover;
}