.navpoint{
    text-decoration: none;
    list-style-type: none;
    cursor: pointer;
}

.navtext:hover{
    color: #14FFEC;
    transition: .6s;
}


.navtext:not(hover){
    color: white;
    transition: .6s;
}

.navtext{
    text-decoration: none;
    color: white;
}

.navtextlock{
    color: #14FFEC;
    text-decoration: none;
}

.navtextlock:after {
    content: "";
    display: flex;
    position: relative;
    bottom: -32px;
    width: 100%;
    height: 3px;
    background: #14FFEC;
}